.offer {
    display: flex;
    place-items: center;
    flex-direction: column;
    background: #F4E1D3;
    padding-top: 2rem;
}
.ads-container {
    display: flex;
    gap: 2rem;
    height: 10rem;
}

@media screen and (max-width: 1023px) {
   .offer {
        place-items: center;
        flex-direction: column;
    }
    .ads-container {
        gap: 1rem;
        transform: scale(0.7);
        height: 8rem;
    } 
}

@media screen and (max-width: 590px) {
    .offer {
        gap: 0;
        height: 28rem;
    }
    .ads-container {
        margin-top: -6rem;
        flex-direction: column;
        height: 28rem;
        gap: 1rem;
        transform: scale(0.6);
    }
}