.contact, .logobox, .whatsappbox, .storebox, .socialmediabox {
    display: flex;
}
.contact {
    align-items: center;
    background: red;
    justify-content: space-around;
}
.logobox {
    gap: 1rem;
    place-items: center;
}
.logobox img, .socialmediabox img {
    height: 26px;
}
.logobox h3 {
    color: white;
    font-weight: 500;
    font-size: 1rem;
}
.whatsappbox, .storebox {
    align-items: center;
    gap: 5px;
}
.socialmediabox {
    gap: 1rem;
}

@media screen and (max-width:950px) {
    .contact {
        flex-direction: column;
    }
} 
@media screen and (max-width:460px) {
    .logobox, .socialmediabox {
        transform: scale(0.8);
    }
}