.footer {
    background: #1F2229;
    display: flex;
    justify-content: space-around;
    padding: 1rem;
}
.footer-left {
    display: flex;
    gap: 5rem;
}
.footer-row {
    display: block;
    text-align: left;
    gap: 1px;
}
.footer-row h3 {
    font-weight: 400;
    font-size: 10px;
    color: white;
}
.footer-row h4 {
    font-weight: 300;
    font-size: 10px;
    color: white;
}
.footer-right img {
    height: 10rem;
    opacity: 0.6;
    padding: 20px;
}


@media screen and (max-width: 666px) {
    .footer-right img {
        position: absolute;
        opacity: 0.1;
        right: 20px;
        height: 7rem;
    }
    .footer-left {
        gap: 3rem;
        transform: scale(0.7);
    }
}