.horizontalbox, .verticalbox {
    outline: 1px solid rgba(0, 0, 0, 0.1);
    display: block;
    gap: 16px;
    background: white;
    border-radius: 10px;
    height: 100%;
    padding: 10px;
}
.verticalbox {
    width: 7rem;
    height: 8rem;
}
.horizontalbox {
    width: 12rem;
}
.logo {
    display: flex;
    align-items: center;
}
.logo img {
    height: 30px;
}
.h-Box-text {
    display: block;
}
.username {
    font-size: 12px;
    text-align: left;
    font-weight: 500;
    margin-bottom: 0.7rem;
}
.tweet {
    font-size: 10px;
    word-wrap: break-word;
    text-align: left;
    font-weight: 200;
}
.photo {
    border: 1px solid #AAAAAA;
    margin-top: 5px;
}
.photo img {
    width: 4rem;
}
