.topbar, .topbar-top, .topbar-top-right, .topbar-menu ul, .topbar-icons, .topbar-menu li {
    display: flex;
}
.topbar {
    background-image: var(--topbarclr);
    height: 100%;
    flex-direction: column;
    padding-bottom: 6vh;
    overflow: hidden;
}
.topbar-top {
    margin: 40px 10rem;
    place-items: center;
    justify-content: space-between;
}
.topbar-top-left img {
    height: 60px;
}
.topbar-top-right {
    gap: 20px;
}
.topbar-menu ul{
    font-size: 12px;
    font-weight: 400;
    gap: 20px;
}
.topbar-menu a {
    text-decoration: none;
    color: white;
}
.topbar-icons {
    gap: 10px;
}
.topbar-icons img, .topbar-icondropdown img {
    background-color: white;
    outline: solid 3px rgba(0, 0, 0, 0.1);
    border-radius: 50%;
    height: 20px;
    padding: 8px;
}
.toggle-button {
    display: none;
}

/* TOPBAR BOTTOM */
.topbar-bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.topbar-bottom-left  {
    color: white;
    display: flex;
    flex-direction: column;
    margin-left: 10rem;
    overflow-wrap: break-word;
    gap: 0;
}
.topbar-bottom-left h6, h1 {
    max-width: 20vw;
    text-align: left;
    font-size: 2.2rem;
}
.topbar-bottom-left h6 {
    font-weight: 200;
    font-size: 10px;
}
.topbar-bottom-left button {
    color: white;
    border: 1px solid white;
    background-image: var(--topbarclr);
    padding: 10px;
    width: 10vw;
    font-weight: 500;
    font-size: 12px;
    border-radius: 20px;
    outline: solid 3px rgba(0, 0, 0, 0.1);  
}
.topbar-bottom-right {
    flex: 1;
}
.openmenu {
    width: 25px;
    height: 25px;
    visibility: visible;
    right: 10px;
    position: absolute;
}
.n-right {
    visibility: hidden;
}
.topbar-menudropdown {
    display: flex;
    place-content: center;
}
.topbar-menudropdown li {
    display: block;
    text-align: left;
    color: black;
    margin-bottom: 0.3rem;
    margin-right: 3rem;
    font-weight: 400;
    font-size: 14px;
}
.topbar-icondropdown{
    display: flex;
    place-content: center;
    gap: 1rem;
    width: 100%;
}



/* MEDIA QUERY FOR TABLET */
@media screen and (max-width: 1023px)  
{
    .topbar-top, .topbar-bottom-left
    {
        margin-left: 2rem;
    }
    .topbar-bottom
    {
        gap: 3rem;
    }
    .topbar-bottom-left h6, h1
    {
        max-width: 40vw;
    }
    .topbar-bottom-left button
    {
        width: 10rem;
    }
}


/* MEDIA QUERY FOR PHONE */
@media screen and (max-width:767px) {
    .topbar-bottom-left
    {
        margin-left: 1rem;
    }
    .topbar-bottom
    {
        transform: scale(0.8);
    }
}
@media screen and (max-width:515px) {
    .topbar-top-right {
        visibility: hidden;
    }
    .t-menu {
        visibility: visible;
    }
    .topbar-bottom
    {
        display: flex;
        flex-direction: column;
        transform: scale(1);
        align-items: center;
    }
    .topbar-bottom-left {
        margin-left: -4rem;
    }
    .topbar-bottom-left h6, h1 {
        max-width: 60vw;
    }
    .topbar-top-left img {
        height: 35px;
    }
    .n-right {
        background: rgba(255, 255, 255, 0.6);
        height: 8rem;
        width: 6rem;
        border-radius: 10px;
        padding: 1rem;
        position: absolute;
        right: 2rem;
        top: 4rem;
        visibility: visible;
    }
    .toggle-button {
        display: flex;
        margin-top: 1rem;
    }
}