.main-container {
    background: #F4E1D3;
    display: flex;
    justify-content: space-evenly;
    place-items: center;
    overflow: auto;
}
.socialmedia {
    display: block;
    padding: 3rem;
}
.cont-one, .cont-two {
    display: flex;
    gap: 1rem;
}
.cont-two {
    margin-top: 1rem;
}
.video-container video {
    border-radius: 10px;
    width: 320px; 
}

@media screen and (max-width:890px) {
    .main-container {
        display: flex;
        flex-direction: column;
    }
}

@media screen and (max-width:470px) {
    .video-container video {
        width: 250px;
    }
    .socialmedia {
        padding: 0;
    }
    .cont-one, .cont-two {
        align-items: center;
        transform: scale(0.6);
        gap: 5px;
    }
    .cont-two {
        margin-top: -2rem;
    }
}